      contacts: [
        {
          icon: 'envelope',
          title: 'Feedback',
          link: 'https://github.com/waylen94'
        },
        {
          icon: 'weibo',
          title: 'Weibo',
          link: 'https://github.com/waylen94'
        },
        {
          icon: 'weixin',
          title: 'Wechat',
          link: 'https://github.com/waylen94'
        }
      ],
      contactStyle: {
        paddingRight: '8px'
      },
      designer: `
        <span style="font-size:0.9em">Designed by
          <span style="color: #e27575;font-size: 14px;">❤</span>
          <a href="https://github.com/waylen94">Weilun</a>
        </span>
