            logo: 'https://cdn.learnku.com/uploads/banners/JpTCK6OKYBIrBIWdtob8.png',
            title: 'Subcribe：SendCloud',
            link: 'https://github.com/waylen94'
          }
        ]
      },
      statistics: {
        title: 'Statistics',
        list: [
          {
            title: 'weilun',
            description: '22889'
          },
          {
            title: 'weilun',
            description: '7397'
          },
          {
            title: 'weilun',
            description: '39375'
          }
        ]
      },
      other: {
        title: 'Other Info',
        list: [
          {
            icon: 'thumbs-up',
            title: 'weilun',
            link: 'https://github.com/waylen94'
          },
          {
            icon: 'globe',
            title: 'weilun',
            link: 'https://github.com/waylen94'
          }
        ]
      }
    }
  }
}
</script>

<style scoped>
a:hover, a:focus { color: #e27575; transition: color .15s;}
</style>



